.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
body{
  margin-top: 1em;
  font-family:'Courier New', Courier, monospace;
 }
 header{
     padding: 30px;
     text-align: center;
     background:radial-gradient(#F7DC6F  50%, #CAF845);
     color: white;
     font-size: 60px;
     text-shadow: 2px 2px black;
     font-weight: bolder;
 }
 .box{
     height: 100%;
     margin: auto;
 }
 .row{
     display: flex;
 
 }
 .col{
     flex: 100%
 }
 .ingred{
    padding: 1em;
    margin-left: 1em;
 }
 #nutrition{
     width: 50px;
     height: 50px;
 }
 .instructions{
     margin-left: 4em;
     margin-right: 4em;
 }
 footer{
  text-align: center;
  padding: 2em;
  background:radial-gradient(#F7DC6F  50%, #CAF845 );
  color: white;
  text-shadow: 2px 2px black;
  font-size: large;
}
 #recipe_pic{
     margin-top: 4em;
 }
 .half{
     background-color:whitesmoke;
 }
 a{
     color: white;
 }
 .row-top{
   margin-top: 30px;
   padding: 5px;
   max-height: 400px;
   min-height: 400px;
 }
 .sug-but{
  padding: 5px;
  margin-top: 10px;
  margin-bottom: 10px;
  }
  .rec-but{
    margin: 5px;
  }
 .recipe_search_box{
     height:100%;
     background:radial-gradient(#F7DC6F  50%, #CAF845);
     border-style: solid;
     border-color: darkblue;
     min-width: 200px;
     max-width: 200px;
     align-items: flex-start;
 }
 .input-ingreds{
     margin-left: 1em;
     font-size: 9px;
 }
.suggestion-box{
     height:100%;
     background:radial-gradient( #CAF845 50%, #F7DC6F );
     border-style: solid;
     border-color: darkblue;
     min-width: 130px;
     max-width: 130px;
 }
 .monday, .tuesday, .wednesday, .thursday, .friday, .saturday, .sunday{
  height:100%;
  background:radial-gradient( #CAF845 50%, #F7DC6F );
  border-style: solid;
  border-color: darkblue;
  min-width: 125px;
  max-width: 125px;
 }
 .recipe-button{
   width: 75%;
 }
 #sub{
     margin-left: 6.5em;
 }
 .drop-diet, .drop-cuisine{
     margin-left: 3em;
 }
 .label-header{
     font-size: 12px;
 }
 #left-side{
   position: relative;
   left:auto;
 }
