	font-family: 'Poppins', sans-serif;
}

.content {
	position: relative;
	width: 100%;
	justify-content: space-between;
	align-items: center;
}

.image {
	position: absolute;
	top: 0;
  padding-left: 70px;
	width: 100%;
	display: flex;
	justify-content: space-between;
	align-items: center;
}

.header .logo {
	position: relative;
	max-width: 80px;
	padding: 30px 100px;
}

.image ul {
	position: relative;
	display: flex;
	right: 150px;
}

.image ul li {
	list-style: none;
}

.image ul li a {
	display: inline-block;
	color: #000000;
	font-weight: 400;
	margin-left: 50px;
	text-decoration: none;
}

.content {
	position: relative;
	width: 100%;
	display: flex;
	justify-content: space-between;
	align-content: center;
}

.text {
	position: relative;
	top: 200px;
	max-width: 600px;
	padding-left: 100px;
}

.text h2 {
	color: #4A8140;
	font-size: 4em;
	font-weight: 700;

}

.text p {
	color: #000000;
}

.image2 {
	position: relative;
	top: 150px;
	width: 600px;
	display: flex;
	justify-content: flex-end;
	padding-right: 50px;
}

.About {
    display: flex;
  }
  
  .About .Container {
    width: 100%;
    margin-top: 130px;
    font-size: 30px;
    margin: 100px 0 0 0;
  }
  
  .About .Container2 {
    width: 50%;
    margin-left: 15%;
    margin-top: 140px;
  }
  
  .About .Photo {
    position: absolute;
    flex-direction: row;
    top: 37%;
    right: 15%;
  }
.contact {
    min-height: 70vh;
    width: 100vw;
    padding: -80px 50px 50px 50px;
    color: rgba(252, 7, 101, 0.753);
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  
  form {
    display: flex;
    flex-direction: column;
    padding: 30px;
    border-radius: 15px;
    width: 300px;
    align-items: left;
    color: black;
    background: white;
  }
  
  input {
    border: none;
    border-bottom: 2px solid rgba(0, 0, 0, 0.6);
    color: black;
    outline: none;
  }
  
  input:-ms-input-placeholder {
    color: rgba(0, 0, 0, 0.8);
    text-align: left;
  }
  
  input::placeholder {
    color: rgba(0, 0, 0, 0.8);
    text-align: left;
  }
  
  input:hover {
    border-bottom: 2px solid rgba(0, 0, 0, 1);
    transform: scale(1.05);
  }
  
  textarea {
    width: 250px;
    height: 250px;
    background: transparent;
    border: none;
    outline: none;
    color: black;
  }
  
  textarea:-ms-input-placeholder {
    color: rgba(0, 0, 0, 0.8);
  }
  
  textarea::placeholder {
    color: rgba(0, 0, 0, 0.8);
  }
  
  form button {
    padding: 7px 10px;
    width: 200px;
    background-color: #990000;
    background-image: linear-gradient(147deg, #990000 0%, #ff0000 74%);
    color: white;
    outline: none;
    border-radius: 17px;
    border: none;
    transition: 0.3s ease;
  }
  
  form button:hover {
    transform: translateY(-10px);
  }
  
  .contact_text a {
    text-decoration: none;
    color: white;
    cursor: pointer;
    font-weight: 600;
  }
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
